body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  min-width: 356px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

img {
  width: 70%;
  max-width: 250px;
  padding: 15px;
  object-fit: cover;
  margin: auto;
  overflow: hidden;
}

td {
  text-align: center;
}

.tr_header {
  font-weight: bold;
  background-color: #999;
  color: #fff;
}

.tr_header th {
  text-align: center;
}

.VersusButton_versusButton__3E1OZ {
  background: none;
  padding: 15px;
  border: 1px solid #666;
  font-weight: bold;
  font-size: 18px;
  cursor: pointer;
}

@media (max-width: 800px) {
  .VersusButton_versusButton__3E1OZ {
    padding: 5px;
    font-size: 14px;
  }
}

.VersusContainer_VersusContainer__12hyb {
  display: flex;
  flex-direction: column;
  text-align: center;
  justify-content: space-evenly;
  align-content: center;
  flex-wrap: wrap;
  margin-top: 25px;
}

.select1,
.select2 {
  min-width: 450px;
  max-width: 500px;
  font-weight: bold;
  font-size: 16px;
  margin: auto;
}

table {
  margin: auto;
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  border-collapse: collapse;
  padding: 25px;
}

table tr {
  border-bottom: 1px solid #999;
}

table th {
  padding: 25px 0;
  padding-right: 10px;
  text-align: left;
}

.marginb th,
.marginb td {
  height: 50px;
}

.marginb {
  border: none;
}

td {
  padding: 0 15px;
}

@media (max-width: 1100px) {
  .media {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  table {
    padding: 10px;
  }

  table th,
  table td {
    font-size: 14px;
  }

  .marginb th,
  .marginb td {
    height: 50px;
  }

  table th {
    padding: 20px 0;
    text-align: left;
  }
  .select1,
  .select2 {
    width: 70vw;
    min-width: 300px;
    font-weight: bold;
    font-size: 14px;
    margin: auto;
  }
  .th_select_header {
    padding: 0;
    text-align: center;
    display: flex;
    align-items: center;
  }

  table h3 {
    font-size: 14px;
  }
}

.baner {
  background: black;
  color: white;
  font-weight: bold;
  margin: auto;
  display: flex;
  justify-content: center;
  text-align: center;
  padding: 25px 0;
  width: 100%;
}

@media (max-width: 800px) {
  h1 {
    font-size: 22px;
  }
}

@media (max-width: 500px) {
  h1 {
    font-size: 14px;
  }
}

.App {
  margin: auto;
  text-align: center;
}

.container {
  margin: auto;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

